body {
    margin: 0;
    padding: 0;
    background-color: #c8965175;
}

.home-poster {
    background: center  rgb(26, 26, 26) no-repeat;
    width: 100%;
    height: 100vh;
    position: relative;
}

.home-poster-layer {
    position: absolute;
    width: 40px;
    height: 70%;
    background-color: rgb(244, 244, 244);
    top:0;
    bottom: 0;
    left: 102px;
    border-radius: 10px;
    margin: auto;
}
.text-deco{
    width: 60%;
    height: 50%;
    position: absolute;
    right: 40px;
    top:0;
    bottom: 0;
    margin: auto;
}
.text-title {
    color: white;
    letter-spacing:1px;
    word-spacing:4px;
    font-size: 52px;
    font-weight: 800;
    margin-bottom: 40px;
}
.text-desc{
    color: rgb(190, 190, 190);
    letter-spacing:1px;
    word-spacing:4px;
    margin-top: 20px;
    font-size: 17px;
    font-weight: 400;
}
.text-title-2 {
    font-weight:800;
    font-size: 38px;
    color: rgb(249, 249, 249);
    letter-spacing:1px;
    word-spacing:4px;
    margin: 23px 0;
}

.div-1 {
    height: 500px;
}
.div-ctn-l {
    position: relative;
    width: 100%;
    height: 550px;
}
.img-layer {
    display: none;
}
.img {
    height: 100%;
